.todo-item {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: calc(7px + 2vmin);
    color: #333333;
}
.todo-item input {
    cursor: pointer;
    flex:1;
    margin-right: 10px;
    font-size: 30px;
    outline: 0;
}
.todo-item .text{
    flex:20;
}

.todo-item .trash{
    cursor: pointer;
    flex:1;
}
.todo-item .trash:hover{
    transform: scale(1.08,1.08);
}


