.todo-app{
    transition: all 0.5s ease-in-out;
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
}
.todo-app .todo-list {
    background-color: white;
    width: 100%;
    display: flex;
    flex-direction: column;
    border: 1px solid #efefef;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15),
    0 10px 0 -5px #eee;
    padding: 30px;
}
.todo-app .addition{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}
.todo-app .addition input{
    background-color: white;
    width: 100%;
    border: 1px solid #efefef;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15),
    0 10px 0 -5px #eee;
    margin: 10px;
    padding: 10px;
    outline: none;
    font-size: calc(5px + 2vmin);
}
.todo-app .addition .submit{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 35px;
    border-radius: 5px;
    background: #282c34;
    color: #eeeeee;
    cursor: pointer;
    padding: 5px;
}
.todo-app .addition .submit:hover{
    opacity: 0.9;

}

@media screen and (max-width: 900px) {
    .todo-app{
        width: 80%;
    }
    .todo-app .addition{
        width: 120%;
    }
}